import React, { useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router';
import { Link } from 'react-router-dom';
// 
import _axios from '../../utils/axios';
// import Alert from '../../components/common/modal/Alert';
//css
import '../../styles/kendo.css';
import {
  Container,
  Wrap,
  LogoWrap,
  Validation,
  CountDown,
  IdBox,
} from '../../styles/account';
import styled from 'styled-components';
//input & button
import Button from '../common/Button';
import Input from '../common/CommonInput';
// import Timer from './Timer';
//icon
import Logo from '../../images/blue_bg.svg';

const JoinPage = () => {
  const navigate = useNavigate(); //   
    // encrypt 
  const [min, setMin] = useState(1);
  const [sec, setSec] = useState(0);
  const time = useRef(60);
  const timerId = useRef(null);
  let yClassList = ['YA', 'YB', 'YC', 'YD'];
  let p1_classList = ['PA', 'PB', 'PC'];
  let p2_classList = ['PA', 'PB', 'PC', 'PE'];
  let qClassList = ['QA', 'QB'];
  let majorList = ['', '', ''];
  let gradeList = ['1', '2', '3'];
  let academicList = ['', '', ''];

  const [state, setState] = useState({
    userId: '',
    userPw: '',
    email: '',
    emailCode:'',   // 
    nickname:'',
    name: '',
    phone:'',
    studentId: '',
    selected: '@m365.dongyang.ac.kr',
    major: '', // 
    grade: '1', // 
    classroom: 'YA',  // 
    academic: '',
    idValidation: '',     //    
    pwValidation: '',
    nicknameValidation: '',
    studentIdValidation: '',
    nameValidation: '',
    phoneValidation: '',
    emailValidation: '',
    codeValidation: '',
    validation: false,    //    
    result: false,        //  axios  
    emailCheck:false,    //   
    codeCheck:false,      //  
    checkButton: false,   //   
    count: 0,             //    
    message: null,
    success: false,       //  /
    visibleDialog: false,
    reCode: false,
    disInput: false,
  });

  //   
  const _handleButtonClick = (e) => {
    e.preventDefault();
    console.log(state.email);
    _postEmail();
    // setState({
    //   ...state,
    //   codeCheck: true,
    // });
  };

  // npm
  const _postEmail = async () => {
    const url = '/mailAuthentication'; 
    const params = {
      address: state.email + state.selected,
    };
    console.log(params);
    const response = await _axios(url, params);
    console.log(response);
    if(response.result){
      setState({
        ...state,
        result: response.result,
        message: response.message,
        codeCheck: true,
        failureCode:false,
        checkButton:false,
        disInput: true,
      });
      alert(' .');
    }else{
      setState({
        ...state,
        result: response.result,
        message: response.message,
      })    
      alert(' !');
    }
  };

   //   
  const _handleButtonCheck = (e) => {
    console.log(state.count);
    e.preventDefault();
    _checkCode();
    // setState({
    //   ...state,
    //   emailCheck: true,
    // });
  };

  // 
  const _checkCode = async () => {
    console.log(state);
    const url = '/confirmation';
    const params = {
      id: state.emailCode,
      address: state.email + state.selected,
    };
    console.log(params);
    const response = await _axios(url, params);
    console.log(response);
    if(response.result){
      setState({
        ...state,
        emailCheck: true,
        result: response.result,
        message: response.message,
      });
      alert(' !');
    }else{
      if(state.count < 4 ){
        setState({
          ...state,
          result: response.result,
          message: response.message,
          failureCode: false,
          disInput: false,
          count: state.count + 1,
      });
      alert(' .');
      }else {
        setState({
          ...state,
          result: response.result,
          message: response.message,
          codeCheck: false,
          count: state.count = 0,
        });
      alert(' .');
        }
    }
    console.log(state.result, state.message);
  };
  
  //  
  let regEng = /^[a-zA-Z0-9]{2,15}$/;         // , 2~15 
  let regNum = /^[0-9]{10,11}$/;            // , 10~11 
  let regCodeNum = /^[0-9]{6,6}$/;          // , 6
  let regStudentNum = /^[0-9]{8,8}$/;          // , 6
  let regString = /^[-a-zA-Z0-9-]+$/; // , 
  let regName = /^[-]+$/; // 
  let regPw = /^[a-zA-Z0-9?!@#$%^&~]*$/;  // , ,  !@#$%^&~ 

  //   
  const _handleInputChange = (e) => {
    console.log(e.target.value);
    switch(e.target.name){
      case 'email' :
        if (regEng.test(e.target.value)) {
          console.log(" .");
          setState({
            ...state,
            emailValidation: '',
            validation: true,
            [e.target.name]: e.target.value 
          });
        }else {
          console.log("   .");
          setState({
            ...state,
            validation: false,
            emailValidation: ' .',
            [e.target.name]: e.target.value 
          });
        }break;
      case 'userId' :
        if (regEng.test(e.target.value)) {
          console.log(" .");
          setState({
            ...state,
            idValidation: '',
            validation: true,
            [e.target.name]: e.target.value 
          });
        }else {
          console.log("   .");
          setState({
            ...state,
            validation: false,
            idValidation: ' .',
            [e.target.name]: e.target.value 
          });
        }break;
      case 'userPw' :
        if (regPw.test(e.target.value)) {
          console.log(" .");
          setState({
            ...state,
            validation: true,
            pwValidation: '',
            [e.target.name]: e.target.value 
          });
        }else {
          console.log("   .");
          setState({
            ...state,
            validation: false,
            pwValidation: ' .',
            [e.target.name]: e.target.value 
          });
        }break;    
      case 'nickname' :
        if (regString.test(e.target.value)) {
          console.log(" .");
          setState({
            ...state,
            validation: true,
            nicknameValidation: '',
            [e.target.name]: e.target.value 
          });
        }else {
          console.log("   .");
          setState({
            ...state,
            validation: false,
            nicknameValidation: ' .',
            [e.target.name]: e.target.value 
          });
        }break;
      case 'name' :
        if (regName.test(e.target.value)) {
          console.log(" .");
          setState({
            ...state,
            validation: true,
            nameValidation: '',
            [e.target.name]: e.target.value 
          });
        }else {
          console.log("   .!");
          setState({
            ...state,
            validation: false,
            nameValidation: ' .',
            [e.target.name]: e.target.value 
          });
        }break;
      case 'phone' :
        if (regNum.test(e.target.value)) {
          console.log("10~11  .");
          setState({
            ...state,
            validation: true,
            phoneValidation: '',
            [e.target.name]: e.target.value 
          });
        }else {
          console.log(" .");
          setState({
            ...state,
            validation: false,
            phoneValidation: ' .',
            [e.target.name]: e.target.value 
          });
        }break;
      case 'studentId' :
        if (regStudentNum.test(e.target.value)) {
          console.log("8  .");
          setState({
            ...state,
            validation: true,
            studentIdValidation: '',
            [e.target.name]: e.target.value 
          });
        }else {
          console.log(" .");
          setState({
            ...state,
            validation: false,
            studentIdValidation: ' .',
            [e.target.name]: e.target.value 
          });
        }break;
      case 'emailCode' :
        if (regCodeNum.test(e.target.value)) {
          console.log(" .");
          setState({
            ...state,
            validation: true,
            codeValidation: '',
            [e.target.name]: e.target.value 
        });
        }else {
          console.log(" .");
          setState({
            ...state,
            validation: false,
            codeValidation: ' .',
            [e.target.name]: e.target.value 
            });
      }break;
        }
      };

  //   
  const _handleSubmit = (e) => {
    e.preventDefault();
      _setData();
  }

  // 
  const _setData = async () => {
    console.log(state);
    const url = '/signUp';
    const params = {
      id: state.email + state.selected,
      password: state.userPw,
      // email: state.email + state.selected,
      nickname: state.nickname,
      phone: state.phone,
      name: state.name,
      studentId: state.studentId,
      classroom: state.classroom,
      major: state.major,
      grade: state.grade,
      academic: state.academic,
    };
    console.log(params);
    const response = await _axios(url, params);
    console.log(response);
    if(response.result){
      setState({
        ...state,
        result: response.result,
        nickname: state.nickname,
        message: response.message,
        success: true,
        visibleDialog: true,
      });
      alert(' !');
      
    }else{
      setState({
        ...state,
        success:false,
        result: response.result,
        message: response.message,
      });
      alert(' !');
    }
  };
  //     
  const _signUpSuccess = (e) => {
    e.preventDefault();
    navigate('/login');
  };

  const _toggleDialog = (e) => {
    e.preventDefault();

    setState({
      ...state,
      visibleDialog: !state.visibleDialog,
    });
  };

  //   
  const Timer = () => {
    useEffect(() => {
        timerId.current = setInterval(() => {
        setMin(parseInt(time.current / 60));
        setSec(time.current % 60);
        time.current -= 1;
      }, 1000);
      return () => clearInterval(timerId.current);
    },[timerId.current]);

    useEffect(() => {
      if(time.current <= 0){
        console.log(" ");
        setMin(0);
        setSec(0);
        clearInterval(timerId.current);
        }
      }, [sec]);
      
      return(
        <Time>
          {min}:{sec}
        </Time>
      );
    };

    const SelectEmail = () => {
      const selectList = ['@m365.dongyang.ac.kr', '@dongyang.ac.kr'];
      const handleSelect = (e) => {
        setState({
          ...state,
          selected: e.target.value,
        });
        console.log(e.target.value);
      };
        return(
          <select onChange={handleSelect} value={state.selected} disabled={state.disInput}>
            {selectList.map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
            ))}
        </select>
        );
      };
    
    const SelectMajor = () => {
      const handleMajor = (e) => {
        setState({
          ...state,
          major: e.target.value,
        });
        console.log(e.target.value);
      };
        return(
          <Selectvalues>
            <select onChange={handleMajor} value={state.major}>
              {majorList.map((item)  => (
                <option value={item} key={item}>
                  {item}
                </option>
              ))}  
            </select> 
          </Selectvalues>
          );
        };

    const SelectGrade = () => {
      const handleGrade = (e) => {
        setState({
          ...state,
          grade: e.target.value,
        });
        console.log(e.target.value);
      };
        return(
          <Selectvalues>
            <select onChange={handleGrade} value={state.grade}>
              {gradeList.map((item) => (
                <option value={item} key={item}>
                  {item}
                </option>
              ))}
            </select>
          </Selectvalues>
        );
    };

    const SelectYClass = () => {
      const handleClass = (e) => {
        setState({
          ...state,
          classroom: e.target.value,
        });
        console.log(e.target.value);  
      }
        return(
          <Selectvalues>
            <select onChange={handleClass} value={state.classroom}> 
              {yClassList.map((item) => (
                <option value={item} key={item}>
                  {item}
                </option>
              ))}
            </select>
          </Selectvalues>
        );
    };

    const SelectP1_class = () => {
      const handleClass = (e) => {
        setState({
          ...state,
          classroom: e.target.value,
        });
        console.log(e.target.value);  
      }
        return(
          <Selectvalues>
            <select onChange={handleClass} value={state.classroom}> 
              {p1_classList.map((item) => (
                <option value={item} key={item}>
                  {item}
                </option>
              ))}
            </select>
          </Selectvalues>
        );
    };
    
    const SelectP2_class = () => {
      const handleClass = (e) => {
        setState({
          ...state,
          classroom: e.target.value,
        });
        console.log(e.target.value);  
      }
      return(
        <Selectvalues>
          <select onChange={handleClass} value={state.classroom}> 
            {p2_classList.map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
            ))}
          </select>
        </Selectvalues>
      );
    };
    
    const SelectQ_class = () => {                  
      const handleClass = (e) => {
        setState({
          ...state,
          classroom: e.target.value,
        });
        console.log(e.target.value);  
      }
      return(
        <Selectvalues>
          <select onChange={handleClass} value={state.classroom}> 
            {qClassList.map((item) => (
          <option value={item} key={item}>
            {item}
          </option>
          ))}
        </select>
      </Selectvalues>
      );
    };
    const SelectAcademic = () => {
      const handleAcademic = (e) => {
        setState({
          ...state,
          academic: e.target.value,
        });
        console.log(e.target.value);
      };
      return(
        <Selectvalues>
          <select onChange={handleAcademic} value={state.academic}>
            {academicList.map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
            ))}
          </select>
        </Selectvalues>
      );
    };

    //    
    const _handleReRequest = (e) => {
      e.preventDefault();
      _postEmail();
      time.current = 60;
      setMin(1);
    };

  return (
    <Container>
    <Wrap>
      <Link to="/">
        <LogoWrap>
          <img src={Logo} alt="logo" width="100%"></img>
        </LogoWrap>
      </Link>
      <form onSubmit={_handleSubmit}>
      <IdBox>
        {!state.emailCheck && (
        <Input
          idName="typepass"
          inputtype="text"
          name="email"
          value={state.email}
          maxLength={20}
          onChange={_handleInputChange}
          required={true}
          titlename=""
          width="100%"
          height="60px"
          margin="0 0 10px 0"
          placeholder="  ."
          validityStyles={false}
          autoComplete="off"
          disabled={state.disInput}
        />)}
        {!state.emailCheck && (
          <SelectBox>
            <SelectEmail/>
          </SelectBox>)}
          </IdBox>
        {!state.emailCheck && (
          <Validation>
            {state.emailValidation}
          </Validation>)}
        <IdBox>
        {!state.emailCheck && state.codeCheck && ( 
        <Input
            idName="typepass"
            inputtype="text"
            name="emailCode"
            value={state.emailCode}
            maxLength={6}
            onChange={_handleInputChange}
            titlename=""
            width="100%"
            height="60px"
            margin="0 0 0 0"
            placeholder=" ."
            validityStyles={false}
            autoComplete="off"
          />)}
        {!state.emailCheck && state.codeCheck && ( 
          <StateBox>
            <Timer/>
              {!state.emailCheck && state.codeCheck && !state.checkButton && time.current === 0 &&(
                <Button
                  margin="0 20px 0 0"
                  width="70%"
                  height="50%"
                  onClickHandler= {_handleReRequest}
                >
                  
                </Button>)}
          </StateBox>)}
          </IdBox>
          {!state.emailCheck && state.codeCheck && (
          <Validation>
            {state.codeValidation}
          </Validation>)}
        {!state.emailCheck && !state.codeCheck && !state.checkButton && (
        <Button
            className="loginAnchor"
            kind="wideBtn_01"
            width="100%!important"
            onClickHandler= {_handleButtonClick}
            disabled={!state.validation}
        >
             
        </Button>)}
        {!state.emailCheck && state.codeCheck && !state.checkButton &&(
        <Button
            className="loginAnchor"
            kind="wideBtn_01"
            width="100%!important"
            onClickHandler= {_handleButtonCheck}
            disabled={!state.validation}
        >
            
        </Button>)}
        {!state.emailCheck && state.codeCheck && !state.checkButton &&(
        <CountDown>
            {state.count}/5
        </CountDown>)}
        {state.emailCheck && !state.success && (
        <Input
          className="input-id"
          inputtype="text"
          width="100%"
          height="60px"
          margin="0 0"
          value={state.email + state.selected}
          maxLength={15}
          autoComplete="off"
          name="userId"
          onChange={_handleInputChange}
          required={true}
          validityStyles={false}
          titlename=""
          disabled={true}
        />)}
        {state.emailCheck && (
          <Validation>
            {state.idValidation}
          </Validation>)}
        {state.emailCheck && !state.success && (
        <Input
          inputtype="password"
          name="userPw"
          value={state.userPw}
          maxLength={15}
          onChange={_handleInputChange}
          required={true}
          titlename=""
          width="100%"
          height="60px"
          margin="10px 0 0 0"
          placeholder=" ."
          validityStyles={false}
          autoComplete="off"
        />)}
        {state.emailCheck && (
          <Validation>
            {state.pwValidation}
          </Validation>)} 
        {state.emailCheck && !state.success && (
        <Input
          inputtype="text"
          name="name"
          value={state.name}
          maxLength={10}
          onChange={_handleInputChange}
          required={true}
          titlename=""
          width="100%"
          height="60px"
          margin="10px 0 0 0"
          placeholder=" ."
          validityStyles={false}
          autoComplete="off"
        />)}
        {state.emailCheck && (
          <Validation>
            {state.nameValidation}
          </Validation>)}
        {state.emailCheck && !state.success && (
        <Input
          inputtype="text"
          name="studentId"
          value={state.studentId}
          maxLength={8}
          onChange={_handleInputChange}
          required={true}
          titlename=""
          width="100%"
          height="60px"
          margin="10px 0 0 0"
          placeholder=" ."
          validityStyles={false}
          autoComplete="off"
        />)}
        {state.emailCheck && (
          <Validation>
            {state.studentIdValidation}
          </Validation>)}
        {state.emailCheck && !state.success && (
        <Input
          inputtype="text"
          name="nickname"
          value={state.nickname}
          maxLength={15}
          onChange={_handleInputChange}
          required={true}
          titlename=""
          width="100%"
          height="60px"
          margin="10px 0 0 0"
          placeholder=" ."
          validityStyles={false}
          autoComplete="off"
        />)}
        {state.emailCheck && (
          <Validation>
            {state.nicknameValidation}
          </Validation>)}
        {state.emailCheck && !state.success && (
        <Input
          inputtype="text"
          name="phone"
          value={state.phone}
          maxLength={11}
          onChange={_handleInputChange}
          required={true}
          titlename=""
          width="100%"
          height="60px"
          margin="10px 0 0 0"
          placeholder="-   ."
          validityStyles={false}
          autoComplete="off"
        />)}
        {state.emailCheck && (
          <Validation>
            {state.phoneValidation}
          </Validation>)}
        {state.emailCheck && !state.success && (
          <ValueBox>
            <SelectMajor/>
            <SelectGrade/>
            { state.major === '' ?
              <SelectYClass/>
            : state.major === '' && state.grade === '1' ?
              <SelectP1_class/>
              : state.major === '' && state.grade === '2' ?
              <SelectP2_class/>
              : state.major === '' ?
              <SelectQ_class/>
              : ''
            }
            <SelectAcademic/>
          </ValueBox>)}
        {state.emailCheck && !state.success &&(
        <Button
          type={'submit'}
          className="loginAnchor"
          kind="wideBtn_01"
          width="100%!important"
          disabled={!state.validation}
        >
          
        </Button>)}
        {state.emailCheck && state.success && (
        <Button
          className="loginAnchor"
          kind="wideBtn_01"
          width="100%!important"
          onClickHandler={_signUpSuccess}
        >
          
        </Button>)}
      </form>
      
      {/* {state.success && state.visibleDialog && (
        <Alert
          kind="alert-4"
          sub1={state.message}
          onClick2={_toggleDialog}
        ></Alert>
      )} */}
    </Wrap>
  </Container>
);
};

const SelectBox = styled.div`
    width: 236px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #A9A9A9;

`;

const StateBox = styled.div`
    width: 275px;
    height: 60px;
    display: flex;
    align-items: center;
    border: 1px solid #A9A9A9;

`;

const Time = styled.div`
  display: flex;
  justify-content: right;
  width: 100%;
`;

const Selectvalues = styled.div`
  display: flex;
  justify-content: space-evenly;
  width: 100%;
`;

const ValueBox = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  border: 1px solid #A9A9A9;
`;

export default JoinPage;